*::selection {
  @include themed() {
    background-color: t('selection');
  }
}

html,
body {
  height: 100%;
}

body {
  width: 100%;
  min-height: 100%;
  margin: 0;

  @include themed(true) {
    background-color: t('bg');
    color: t('text');
  }
}

.js-dark-mode-toggler {
  position: absolute;
  top: 2rem;
  right: 2rem;
  display: block;
  width: 2em;
  height: 2em;
  background-size: contain;
  background-position: center center;
  background-color: transparent;

  &[data-mode='light'] {
    background-image: url('./images/bat.svg');
  }

  &[data-mode='dark'] {
    background-image: url('./images/sunny.svg');
  }
}

.wrapper {
  max-width: 100%;
}

.content {
  width: 100%;
  max-width: $screen-md-min;
  margin: 0 auto;
  text-align: left;
}

img {
  display: block;
}

ul {
  padding: 0;
  list-style: none;
}

address {
  font-style: normal;
}
