$themes: (
  light: (
    bg: #fff,
    text: #3d4451,
    textSecondary: #666,
    divider: #eee,
    section: #fdfdfd,
    selection: #fff0f5,
    link: #11669e,
    github: url('images/github-dark.svg'),
    disabled: #e0e0e0
  ),
  dark: (
    bg: #121212,
    text: #e2e2e2,
    textSecondary: #a2a2a2,
    divider: lighten(#121212, 7%),
    section: darken(#121212, 1%),
    selection: grey,
    link: #61dafb,
    github: url('images/github-light.svg'),
    disabled: #a2a2a2
  )
);

@mixin themed($isRoot: false) {
  @each $theme, $map in $themes {
    @if ($isRoot) {
      &.theme--#{$theme} {
        $theme-map: () !global;
        @each $key, $submap in $map {
          $value: map-get(map-get($themes, $theme), '#{$key}');
          $theme-map: map-merge(
            $theme-map,
            (
              $key: $value
            )
          ) !global;
        }
        @content;
        $theme-map: null !global;
        transition: background-color 200ms ease, border-color 200ms ease;
      }
    } @else {
      .theme--#{$theme} & {
        $theme-map: () !global;
        @each $key, $submap in $map {
          $value: map-get(map-get($themes, $theme), '#{$key}');
          $theme-map: map-merge(
            $theme-map,
            (
              $key: $value
            )
          ) !global;
        }
        @content;
        $theme-map: null !global;
        transition: background-color 200ms ease, border-color 200ms ease;
      }
    }
  }
}

@function t($key) {
  @return map-get($theme-map, $key);
}
