section {
  display: flex;
  align-items: center;
  min-height: 50%;
  padding: 5rem 2.5rem;

  @include md {
    padding: 15rem 10rem;
  }

  &:not(:last-child) {
    border-bottom: 1px solid transparent;

    @include themed() {
      border-bottom: 1px solid t('divider');
    }
  }

  &:nth-child(2n) {
    @include themed() {
      background-color: t('section');
    }
  }
}
